@import url("https://fonts.googleapis.com/css?family=Lato&display=swap");

:root {
  --box-shadow: 0 0.0625rem 0.1875rem rgba(0, 0, 0, 0.12),
    0 0.0625rem 0.125rem rgba(0, 0, 0, 0.24);
}

* {
  box-sizing: border-box;
}

body {
  position: relative;
  background-color: #f7f7f7;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  margin: 0;
  font-family: "Lato", sans-serif;
  background: linear-gradient(to right, #0d7377, #827397, #115173);
  background-size: 400% 400%;
  animation: container 15s infinite ease-in-out;
}

@keyframes container {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}

.container {
  margin: 1.875rem auto;
  width: 28rem;
}

h1 {
  letter-spacing: 0.0625rem;
  margin: 0;
}

h2 {
  font-size: 2.5rem;
  margin: 1rem 0;
  color: #fff8dc;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

h3 {
  border-bottom: 0.2rem solid rgba(0, 255, 255, 0.678);
  padding-bottom: 0.625rem;
  margin: 1.5rem 0 0.625rem;
  color: #fff8dc;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 1.75rem;
}

h4 {
  margin: 0 1.5rem;
  text-transform: uppercase;
  color: white;
}

.tab-container button {
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
  background-color: gray;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  cursor: pointer;
}

.svg {
  pointer-events: none;
}

.double-container {
  background-color: #1f4058;
  box-shadow: var(--box-shadow);
  padding: 1.25rem;
  display: flex;
  justify-content: space-between;
  margin: 0.75rem 0;
  border-radius: 2rem/2rem;
}

.double-container > div {
  flex: 1;
  text-align: center;
}

.double-container > div:first-of-type {
  border-right: 0.1rem solid #dedede;
}

.single-container {
  background-color: #1f4058;
  box-shadow: var(--box-shadow);
  padding: 1.25rem;
  display: flex;
  justify-content: space-between;
  margin: 0.75rem 0;
  border-radius: 2rem/2rem;
}

.single-container > div {
  flex: 1;
  text-align: center;
}

.money {
  font-size: 1.25rem;
  letter-spacing: 0.0625rem;
  margin: 0.3125rem 0;
}

.money.plus {
  color: rgb(46, 204, 112);
}

.money.minus {
  color: rgb(191, 58, 43);
}

label {
  display: inline-block;
  margin: 0.625rem 0;
}

input[type="text"],
input[type="number"] {
  border: 0.0625rem solid #dedede;
  border-radius: 0.5rem;
  display: block;
  font-size: 1rem;
  padding: 0.625rem;
  width: 100%;
}

.btn {
  font-size: 2rem;
  font-weight: 700;
  cursor: pointer;
  background-color: #1f4058;
  box-shadow: 0 0 5px cyan, 0 0 25px cyan;
  color: white;
  border: 0;
  display: block;
  font-size: 1rem;
  margin: 0.625rem 0 1.875rem;
  padding: 0.625rem;
  width: 100%;
  border-radius: 0.5rem;
  transition-property: transform;
  transition-duration: 0.3s;
  transition-timing-function: ease;
  transition-delay: 0s;
}

.btn:hover {
  transform: translateY(5px);
  background-color: rgb(46, 204, 112);
  color: black;
  box-shadow: 0 0 5px rgb(46, 204, 112), 0 0 25px rgb(46, 204, 112),
    0 0 50px rgb(46, 204, 112), 0 0 100px rgb(46, 204, 112),
    0 0 200px rgb(46, 204, 112);
}

.btn:focus,
.delete-btn:focus {
  outline: 0;
}

.list {
  list-style-type: none;
  padding: 0;
  margin-bottom: 1rem;
}

.list li {
  background-color: #fff;
  box-shadow: var(--box-shadow);
  border-right: 0.4125rem solid rgb(238, 138, 138);
  color: #333;
  display: flex;
  justify-content: space-between;
  position: relative;
  padding: 0.625rem;
  margin: 0.625rem 0;
  border-radius: 0.5rem/0.5rem;
}

.car-list {
  display: flexbox;
  font-size: 1.75rem;
  border: solid cyan;
  border-radius: 1rem;
  box-shadow: 1rem 0.5rem 0.25rem cyan;
  color: #fff8dc;
  background-color: rgb(191, 58, 43);
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.car-list li {
  color: #fff8dc;
  background-color: #1f4058;
  padding: 0.625rem;
  position: relative;
  border-radius: 1rem;
  font-size: 1.11rem;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.delete-btn {
  cursor: pointer;
  background-color: rgb(231, 76, 60);
  border: 0;
  color: #fff;
  font-size: 2.5rem;
  line-height: 1.25rem;
  padding: 0.125rem 0.3125rem;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-100%, -50%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.list li:hover .delete-btn {
  opacity: 1;
}

.car-list li:hover .delete-btn {
  background-color: #9a8baf;
  opacity: 1;
}

.arrow {
  position: relative;
  width: 1.5rem;
  height: 1.5rem;
}

.arrow::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  border: solid #fff8dc;
  border-width: 0 0.1875rem 0.1875rem 0;
  padding: 0.5rem;
  display: inline-block;
  cursor: pointer;
  transition: transform 0.2s;
}

.arrow:hover::before {
  transform: translateX(-0.3125rem) translateY(-50%) rotate(135deg);
}

@media (min-width: 30rem) {
  .container {
    width: 28rem;
  }
}

@media (min-width: 20rem) and (max-width: 29rem) {
  .container {
    width: 18.75rem;
  }
}
